import React from 'react';
import './App.css';
import Home from './Home/Home';
import NavBar from './Home/NavBar';
import HomeBody from './Home/HomeBody';
import videohome from './videosection/videohome';
import gamingSections from './videosection/gamingSections';
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import GrouplandingPage from './FaceGropus/GrouplandingPage';

function App() {
  return (
    <Router>
      <div className="App">
        <NavBar />
        <div style={{ height: "8vh" }}></div>
        <div>
          <Switch>
            <Route exact path="/facebook-clone/" component={HomeBody} />
            <Route exact path="/facebook-clone/ba" component={videohome} />
            <Route exact path="/facebook-clone/ca" component={GrouplandingPage} />
            <Route exact path="/facebook-clone/da" component={gamingSections} />
            <Redirect from="*" to="/facebook-clone/" />
          </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;
