import React, { Component } from 'react'
import './navbar.css';
import { Link } from 'react-router-dom';
import AddnewPost from '../AddnewFile/AddnewPost';
import Notifications from '../AddnewFile/Notifiactions';
class NavBar extends Component {
    state = {
        showDis: "none",
        ad: 0,
        nf: 0
    }
    render() {
        const styleBack = {
            display: this.state.showDis
        }
        return (
            <div>

                <div className="full-navbar">
                    <div className="navbar-icon">
                        <div className="facebook-image">
                            <img src="data:image/png;base64," />
                        </div>
                        <input style={{paddingLeft:"0.5rem"}} type="text" placeholder="search facebook" />
                    </div>

                    <div className="navbar-activity">
                        <div className="option-menus">
                            <Link to={'/facebook-clone/'} className="op-menu">
                                <i style={{ color: "Blue", fontSize: "1.5rem" }} class="fas fa-home"></i>
                            </Link>
                            <Link to={'/facebook-clone/ba'} className="op-menu">
                                <i style={{ color: "Blue", fontSize: "1.5rem" }} class="fab fa-youtube"></i>
                            </Link>
                            <Link to={'/facebook-clone/ca'} className="op-menu">
                                <i style={{ color: "Blue", fontSize: "1.5rem" }} class="fas fa-users"></i>
                            </Link>
                            <Link to={"/facebook-clone/da"} className="op-menu">
                                <i style={{ color: "Blue", fontSize: "1.5rem" }} class="fas fa-dice-d6"></i>
                            </Link>
                        </div>
                    </div>


                    <div className="navbar">
                        < a onClick={() => (
                            this.setState({
                                showDis: ((this.state.ad === 1) ? (this.state.showDis = "none") : (this.state.showDis = "block")),
                                ad: ((this.state.ad === 1) ? (this.state.ad = 0) : (this.state.ad = 1)),
                                nf: 0
                            })
                        )}>
                            <div className="navbar-circle"> <i class="fas fa-plus"></i>
                            </div>
                        </a>
                        <a onClick={() => (
                            this.setState({
                                showDis: ((this.state.nf === 1) ? (this.state.showDis = "none") : (this.state.showDis = "block")),
                                ad: 0,
                                nf: ((this.state.nf === 1) ? (this.state.nf = 0) : (this.state.nf = 1))
                            })
                        )}>
                            <div className="navbar-circle">
                                <i class="fas fa-bell"></i>
                            </div>
                        </a>
                        <a>
                            <div className="navbar-circle">
                                <i class="fab fa-facebook-messenger"></i>
                            </div>
                        </a>
                    </div>
                    <div style={styleBack} className="createShadow"
                        onClick={() => (
                            this.setState({
                                showDis: (this.state.showDis === "none" ? this.state.showDis = "block" : this.state.showDis = "none"),
                                ad: 0,
                                nf: 0
                            })
                        )}>
                    </div>
                    {
                        (this.state.ad === 1) ? (<AddnewPost />) : (null)
                    }
                    {
                        (this.state.nf === 1) ? (<Notifications />) : (null)
                    }
                </div>
            </div>
        )
    }
}

export default NavBar;
