import React from 'react'
import './addnewpost.css';
function Notifiactions() {
    const notifiArray = ["Sharma has added you in his story", "We've reviewed you report", "Thanks for reporting, we wiil surely get to you", "Your new photo has got 100 likes","BOB liked your profile photo","John Doe commented on your cover photo 'Awesome' "];
    return (
        <div className="notiDiv">
            {
                notifiArray.map(not => (
                    <div style={{ padding: "1rem",paddingBottom:"1rem", borderBottom: "1px solid #ccc", lineHeight:"1.25rem" }}>
                        <div className="user-pics"></div><div style={{marginLeft:"15%"}}> {not}</div>
                    </div>
                ))
            }
        </div>
    )
}
export default Notifiactions;
