import React, { Component } from 'react'
import './grouplandingpage.css';
import GroupCard from './GroupCard';
class GrouplandingPage extends Component {
    render() {
        return (
            <div>
                <div className="GLanding">
                    <div className="added-group">
                        <div className="added-group-content">
                            <span style={{ fontWeight: "bold", fontSize: "1.5rem" }}>Groups</span>
                            <br />
                            <input type="text" placeholder="Search group" className="new-group-search" />
                            <input type="submit" value="+ Create New Group" className="new-group-submit" />
                        </div>
                        <hr />
                        <div className="your-groups">
                            <span style={{ fontWeight: "bold", fontSize: "1.25rem" }}>Your groups</span>
                        </div>
                    </div>
                    <div className="list-groups">
                        <div className="friend-group">
                            <span style={{ fontWeight: "bold", fontSize: "1.25rem" }}>Friends' groups</span>
                            <br />
                            Groups your friends' are in.
                            <div className="adding-card">
                                <div className="card-1">
                                    <GroupCard name="Yoga Class" url="https://content.jdmagicbox.com/def_content/yoga_classes/default-yoga-classes-1.jpg" />
                                </div>
                                <div className="card-2">
                                    <GroupCard name="Friends Zone" url="https://www.iwmbuzz.com/wp-content/uploads/2019/09/a-diehard-fan-of-taarak-mehta-ka-ooltah-chashmah-take-a-test.jpg"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default GrouplandingPage;