import React, { Component } from 'react';
import './homebody.css';
import NewPhotofeed from './NewPhotofeed';
import FeaturesApp from './FeaturesApp';
import Contacts from './Contacts';
class HomeBody extends Component {
    render() {
        return (
            <div>
                <div className="homebody">
                    <div className="features">
                        <FeaturesApp />
                    </div>
                    <div className="newsfeed">
                        <div className="newfeed-card">
                            <div className="newfeed-up">
                                <div className="newfeed-user">
                                    <div className="user-pic"></div>
                                </div>
                                <div className="newfeed-input">
                                    <input style={{fontSize:"1.25rem"}} type="text" placeholder="what's in your mind" />
                                </div>
                                <hr />
                            </div>
                            <div className="newfeed-up">
                                <div className="extra-feed-option">
                                    <i style={{ color: "red", fontSize: "1.1rem" }} class="fas fa-video"></i> Live Video
                                </div>
                                <div className="extra-feed-option">
                                    <i style={{ color: "green", fontSize: "1.1rem" }} class="fas fa-images"></i> Photo/Video
                                </div>
                                <div className="extra-feed-option">
                                    <i style={{ color: "yellow", fontSize: "1.1rem" }} class="far fa-surprise"></i> feeling/activity
                                </div>
                            </div>
                        </div>
                        <NewPhotofeed />
                    </div>
                    <div className="contacts">
                        <Contacts />
                    </div>
                </div>
            </div>
        )
    }
}
export default HomeBody;