import React from 'react';
import './contacts.css';

function Contacts() {
    return (
        <div style={{marginTop:"2rem    "}}>
            <span style={{fontSize:"1.25rem", color:"black", fontWeight:"bold", padding:"1rem"}}>Contacts</span>
            <div className="contact">
                <div className="user-pics"></div>
                <div className="user-names">Anurag Bhardwaj</div>
            </div>
        </div>
    )
}

export default Contacts;