import React, { Component } from 'react';
import '../FaceGropus/grouplandingpage.css';
import './videos.css';
import pixelarr from './videosJson';

class videohome extends Component {
    render() {
        return (
            <div>
                <div className="GLanding">
                    <div className="added-group">
                        <div className="added-group-content">
                            <span style={{ fontWeight: "bold", fontSize: "1.5rem" }}>Watch</span>
                            <br />
                            <input type="text" placeholder="Search video" className="new-group-search" />
                            <ul style={{ fontWeight: "600" }}>
                                <li style={{ background: "aquamarine" }}> <i style={{ fontSize: "1.5rem", color: "blue" }} class="fab fa-youtube"></i>Home</li>
                                <li><i style={{ fontSize: "1.5rem", color: "red" }} class="fab fa-playstation"></i> Shows</li>
                                <li> <i style={{ fontSize: "1.5rem", color: "orange" }} class="fas fa-video"></i> Live</li>
                                <li><i style={{ fontSize: "1.5rem", color: "#ccc" }} class="fas fa-tags"></i> Saved videos</li>
                            </ul>
                        </div>
                        <hr />
                        <div className="your-groups">
                            <span style={{ fontWeight: "bold", fontSize: "1.25rem" }}>Your watchlist</span>
                        </div>
                    </div>
                    <div className="list-groups">
                        {console.log(pixelarr.categories[0].videos)}
                        <div className="videosarea">
                            {
                                pixelarr.categories[0].videos.map(vid => (
                                    <div className="video-one" style={{marginTop:"2vh", marginBottom:"4vh", background:"white"}}>
                                        <div className="video-heading">{vid.title}</div>
                                        <video width="90%" height="340" controls>
                                            <source src={vid.sources[0]} type="video/mp4" />
                                            <source src="movie.ogg" type="video/ogg" />
                                                Your browser does not support the video tag.
                                    </video>
                                    </div>
                                ))
                            }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default videohome;